import { FormErrors } from '../errors/validation.error';
import { TaskForm } from '../forms/task.forms';
import { createValidator, ValidationRules } from '../utils/validation.utils';

export const validateTaskForm = (form: TaskForm): FormErrors<TaskForm> => {
  const errors: FormErrors<TaskForm> = {};

  errors.ticketId = createValidator([
    { check: (v) => v > 0, message: 'Ticket ID is required' }
  ])(form.ticketId);

  errors.title = createValidator([
    { check: (v) => ValidationRules.required(v), message: 'Title is required' },
    { check: (v) => ValidationRules.minLength(v, 2), message: 'Title must be at least 2 characters' },
    { check: (v) => ValidationRules.maxLength(v, 200), message: 'Title must be less than 200 characters' }
  ])(form.title);

  errors.description = createValidator([
    { check: (v) => !v || ValidationRules.minLength(v, 5), message: 'Description must be at least 5 characters' },
    { check: (v) => !v || ValidationRules.maxLength(v, 1000), message: 'Description must be less than 1000 characters' }
  ])(form.description);

  errors.dueDate = createValidator([
    { check: (v) => !v || ValidationRules.required(v), message: 'Due date is required' },
    { check: (v) => !v || ValidationRules.date(v), message: 'Please enter a valid date' },
  ])(form.dueDate);

  errors.assignee = null; 
  errors.status = null; 

  return errors;
};